.menu {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(50px);
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  z-index: 4;
  padding-top: rem(304);

  @include media-breakpoint-down(tab) {
    padding-top: rem(100);
  }

  &--active {
    display: flex;
  }

  a {
    font-family: $font-family-msc7;
    font-size: rem(50);
    line-height: rem(60);
    text-align: center;
    color: #303030;
    text-decoration: none;
    margin-bottom: rem(37);

    @include media-breakpoint-down(tab) {
      font-size: rem(20);
      line-height: rem(24);
      margin-bottom: rem(15);
    }

    &:hover {
      text-decoration: underline;
    }
  }

  &__links {
    display: flex;

    .header__info {
      margin: 0 rem(14);
    }
  }

  .header__phone, .header__info {
    display: none;

    @include media-breakpoint-down(tab) {
      display: block;
    }
  }
}

.body--overflow {
  overflow: hidden;
}



