.footer {
  &__inner {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: rem(20) 0;
    align-items: flex-start;

    @include media-breakpoint-down(tab) {
      padding-right: rem(12);
      padding-bottom: rem(6);
    }
  }

  &__left {
    font-family: $font-family-msc3;
    font-size: rem(18);
    line-height: rem(27);
    color: #303030;
  }

  &__right {
    svg {
      width: rem(38);
      height: rem(38);
    }
  }
}



