.header {
  position: fixed;
  top: 0;
  width: 100%;
  padding: rem(24) 0 rem(24) 0;
  z-index: 5;

  &__inner {
    justify-content: space-between;
    align-items: center;
    display: flex;
  }

  @include media-breakpoint-down(tab) {
    padding: rem(0) rem(14);
  }

  &--active {
    background: $white;

    .header__logo {
      text-decoration: none;
      svg {
        path {
          fill: $black;
        }
      }
    }

    .header__phone {
      color: #303030;
    }

    .header__menu {
      cursor: pointer;
      svg {
        path {
          fill: $black;
        }
      }
    }
  }

  &__logo {
    svg {
      width: rem(219);
      height: rem(58);

      @include media-breakpoint-down(tab) {
        width: rem(102);
        height: rem(27);
      }
    }
  }

  &__info-block {
    display: flex;
    align-items: center;
  }

  &__info {
    margin-left: rem(42);

    &:last-of-type {
      margin-left: rem(28);
    }

    @include media-breakpoint-down(tab) {
      display: none;
    }
  }

  &__phone {
    font-family: $font-family-msc7;
    font-size: rem(24);
    line-height: rem(29);
    color: $white;
    text-decoration: none;

    svg {
      width: rem(24);
      height: rem(24);
      margin-right: rem(20);
    }

    @include media-breakpoint-down(tab) {
      display: none;
    }
  }

  &__menu {
    margin-left: rem(58);

    svg {
      height: rem(80);
    }

    path {
      transition: .4s;
    }

    &--active {
      path {
        &:first-child {
          transform: rotate(-45deg) translate(-14px, 24px);
        }

        &:last-child {
          transform: rotate(45deg) translate(2px, -23px);
        }
      }
    }
  }
}



