.main {
  &__first-block {
    padding-top: rem(190);
    width: 100%;
    height: rem(960);
    background: url("../images/background.jpg") no-repeat center center;
    background-size: cover;

    @include media-breakpoint-down(tab) {
      padding-top: rem(159);
      height: rem(300);
      margin-bottom: rem(300);
    }
  }

  &__header {
    font-family: $font-family-bnb;
    font-size: rem(90);
    line-height: rem(90);
    text-transform: uppercase;
    color: $white;

    @include media-breakpoint-down(tab) {
      font-size: rem(50);
      line-height: rem(50);
    }
  }

  &__small-header {
    font-family: $font-family-msc7;
    font-size: rem(50);
    line-height: rem(60);
    color: $white;
    margin-top: rem(10);

    @include media-breakpoint-down(tab) {
      font-size: rem(20);
      line-height: rem(24);
      margin-top: rem(5);
    }

    span {
      color: $red;
    }
  }

  .list-main {
    list-style-type: none;
    padding-left: rem(4);
    margin-top: rem(48);

    @include media-breakpoint-down(tab) {
      position: absolute;
      top: rem(350);
    }

    li {
      display: flex;
      align-items: center;
      margin-top: rem(6);
    }

    svg {
      @include media-breakpoint-down(tab) {
        width: 5%;
      }
    }

    &__icon {
      display: flex;
      align-items: center;
      justify-content: center;
      width: rem(70);
      height: rem(70);

      @include media-breakpoint-down(tab) {
        width: rem(46);
        height: rem(46);

        svg {
          width: 100%;
        }
      }

      &--first {
        svg {
          width: rem(70);
          height: rem(70);
        }
      }

      &--second {
        svg {
          width: rem(70);
          height: rem(70);
        }
      }

      &--third {
        svg {
          width: rem(31);
          height: rem(40);
        }
      }

      &--fourth {
        svg {
          width: rem(70);
          height: rem(70);
        }
      }
    }

    &__big-text {
      font-family: $font-family-msc7;
      font-size: rem(36);
      line-height: rem(43);
      color: $white;
      background: $red;
      border-radius: rem(4);
      margin-left: rem(50);
      padding: rem(2) rem(8);

      @include media-breakpoint-down(tab) {
        font-size: rem(20);
        line-height: rem(24);
        margin-left: rem(8);
      }
    }

    &__small-text {
      font-family: $font-family-msc7;
      font-size: rem(24);
      line-height: rem(29);
      color: $white;
      margin-left: rem(50);

      @include media-breakpoint-down(tab) {
        font-size: rem(14);
        line-height: rem(17);
        margin-left: rem(8);
        color: #303030;
      }
    }
  }

  &__second-block {
    padding-top: rem(90);

    @include media-breakpoint-down(tab) {
      padding-top: rem(70);
    }
  }

  &__header-middle {
    font-family: $font-family-bnb;
    font-size: rem(70);
    line-height: rem(70);
    text-align: center;
    text-transform: uppercase;
    color: $red;

    @include media-breakpoint-down(tab) {
      font-size: rem(40);
      line-height: rem(40);
    }
  }

  &__third-block {
    background: linear-gradient(180deg, #FDF4F4 0%, #ECF3EC 100%);
    margin-top: rem(84);
    padding-top: rem(70);
    padding-bottom: rem(78);

    @include media-breakpoint-down(tab) {
      margin-top: rem(60);
      padding-top: rem(40);
      padding-bottom: rem(30);
    }
  }
}

.btn {
  font-family: $font-family-msc7;
  font-size: rem(19);
  line-height: rem(23);
  text-align: center;
  letter-spacing: 0.155em;
  text-transform: uppercase;
  margin-top: rem(96);
  padding: rem(22) rem(80);
  border-radius: rem(4);
  text-decoration: none;

  &--red {
    border: 1px solid transparent;
    background: $red;
    color: $white;

    @include media-breakpoint-down(tab) {
      position: absolute;
      top: rem(220);
      width: 94%;
      padding-left: 0;
      padding-right: 0;
      display: flex;
      justify-content: center;
      font-size: rem(16);
      line-height: rem(19);
    }

    &:hover {
      border: 1px solid $red;
      background: transparent;
      color: $red;
    }
  }
}

.slider-second {
  position: relative;
  overflow: hidden;

  &:before {
    position: absolute;
    top: rem(58);
    content: '';
    width: rem(52);
    height: rem(630);
    border: rem(30) solid $red;
    border-right: none;
    left: -1px;
    z-index: 2;

    @include media-breakpoint-down(tab) {
      display: none;
    }
  }

  &:after {
    position: absolute;
    top: rem(58);
    right: -1px;
    content: '';
    width: rem(52);
    height: rem(630);
    border: rem(30) solid $red;
    border-left: none;
    z-index: 2;

    @include media-breakpoint-down(tab) {
      display: none;
    }
  }

  &__link {
    display: none;

    @include media-breakpoint-down(tab) {
      display: flex;
      align-items: center;
      justify-content: center;
      font-family: $font-family-msc7;
      font-size: rem(13);
      line-height: rem(16);
      text-align: center;
      letter-spacing: 0.155em;
      text-transform: uppercase;
      color: #888888;
      width: rem(156);
      height: rem(40);
      border: 1px solid #888888;
      box-sizing: border-box;
      border-radius: rem(4);
      margin: rem(44) auto 0;
    }
  }

  .swiper-slide {
    display: flex;
    padding: rem(58) rem(148) rem(100);

    @include media-breakpoint-down(tab) {
      flex-direction: column-reverse;
      padding: 0;
      padding-bottom: rem(68);
    }
  }

  &__left {
    width: 62%;
    padding-top: rem(37);

    @include media-breakpoint-down(tab) {
      width: 100%;
      text-align: center;
      padding-top: rem(28);
    }
  }

  &__right {
    width: 38%;

    img {
      width:100%;
    }

    @include media-breakpoint-down(tab) {
      width: 100%;
      padding-top: rem(32);
      text-align: center;

      img {
        width: rem(128);
        height: rem(128);
      }
    }
  }

  &__header {
    font-family: $font-family-bnb;
    font-size: rem(70);
    line-height: rem(70);
    text-transform: uppercase;
    color: $red;

    @include media-breakpoint-down(tab) {
      font-size: rem(40);
      line-height: rem(40);
    }
  }

  &__small-header {
    font-family: $font-family-bnb;
    font-size: rem(40);
    line-height: rem(40);
    text-transform: uppercase;
    color: #888888;
    margin-top: rem(4);

    @include media-breakpoint-down(tab) {
      font-size: rem(30);
      line-height: rem(30);
      margin-top: rem(15);
    }
  }

  &__text {
    font-family: $font-family-msc3;
    font-size: rem(13);
    line-height: rem(16);
    letter-spacing: 0.155em;
    text-transform: uppercase;
    color: #303030;
    margin-top: rem(16);

    @include media-breakpoint-down(tab) {
      margin-top: rem(21);
    }
  }

  &__list {
    list-style-type: none;
    padding-left: 0;
    margin-top: rem(40);

    @include media-breakpoint-down(tab) {
      margin-top: rem(24);
    }

    li {
      position: relative;
      padding-left: rem(38);
      margin-top: 1px;

      @include media-breakpoint-down(tab) {
        text-align: left;
        padding-left: rem(21);
        margin-bottom: rem(31);
      }

      &:before {
        position: absolute;
        left: -2px;
        top: rem(10);
        content: '';
        width: rem(10);
        height: rem(10);
        background: $red;
        border-radius: rem(3);

        @include media-breakpoint-down(tab) {
          left: 0;
          top: rem(8);
        }
      }

      span {
        font-family: $font-family-msc3;
        font-size: rem(18);
        line-height: 160%;
        color: #303030;
      }
    }
  }

  &__info-block {
    display: flex;
    flex-direction: column;
    margin-top: rem(30);

    @include media-breakpoint-down(tab) {
      text-align: left;
      margin-top: rem(45);
    }

    span {
      max-width: rem(670);

      &:first-child {
        font-family: $font-family-msc7;
        font-size: rem(18);
        line-height: 160%;
        color: $black;
      }

      &:last-child {
        font-family: $font-family-msc3;
        font-size: rem(18);
        line-height: 160%;
        color: #303030;
      }
    }
  }

  .swiper-pagination {
    position: absolute;
    bottom: rem(4);
    font-family: $font-family-msc7;
    font-size: rem(24);
    line-height: rem(29);
    text-align: center;
    color: #303030;
    left: 50%;
    transform: translate(-50%, 0px);
    z-index: 3;
  }

  .swiper-button-prev, .swiper-button-next {
    width: rem(30);
    height: rem(25);
    background: none;
    bottom: rem(4);
    top: auto;
    z-index: 3;

    svg {
      width: rem(30);
      height: rem(25);
    }

    &:hover {
      path {
        fill: $red;
      }
    }
  }

  .swiper-button-prev {
    left: 43.5%;

    @include media-breakpoint-down(tab) {
      left: 17.5%;
    }
  }

  .swiper-button-next {
    right: 43.5%;
    transform: rotate(180deg);

    @include media-breakpoint-down(tab) {
      right: 17.5%;
    }
  }
}

.card {
  display: flex;
  flex-wrap: wrap;
  margin: rem(44) rem(-18) 0;

  @include media-breakpoint-down(tab) {
    flex-direction: column;
    margin-top: rem(14);
    padding: 0 rem(10);
  }

  &__element {
    width: 25%;
    margin-bottom: rem(68);

    @include media-breakpoint-down(tab) {
      width: 100%;
      margin-bottom: rem(20);
    }
  }

  &__img {
    img {
      width: 100%;
      object-fit: contain;
    }
  }

  &__header {
    font-family: $font-family-msc7;
    font-size: rem(18);
    line-height: 160%;
    color: #303030;
    margin-top: rem(12);
    padding-left: rem(16);
    max-width: rem(422);
  }

  &__text {
    font-family: $font-family-msc3;
    font-size: rem(18);
    line-height: 160%;
    color: #303030;
    margin-top: rem(12);
    padding-left: rem(16);
    max-width: rem(428);
  }
}

.card-links {
  display: none;

  @include media-breakpoint-down(tab) {
    display: flex;
    flex-wrap: wrap;
    margin-top: rem(28);
    justify-content: center;
  }

  .card-link {
    width: rem(63);
    height: rem(40);
    background: #FFFFFF;
    border: 1px solid #888888;
    box-sizing: border-box;
    border-radius: 4px;
    font-family: $font-family-msc7;
    font-size: rem(13);
    line-height: rem(16);
    text-align: center;
    letter-spacing: 0.155em;
    text-transform: uppercase;
    color: #888888;
    margin: 0 rem(5);
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: rem(9);

    &.active {
      background: $red;
      color: $white;
    }
  }
}



